@font-face {
  font-family: "Formular";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/formular/Formular.woff2") format("woff2"), url("../fonts/formular/Formular.woff") format("woff");
}
@font-face {
  font-family: "Formular";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/formular/Formular-Bold.woff2") format("woff2"), url("../fonts/formular/Formular-Bold.woff") format("woff");
}
@font-face {
  font-family: "Formular";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/formular/Formular-Light.woff2") format("woff2"), url("../fonts/formular/Formular-Light.woff") format("woff");
}
.container {
  max-width: 1500px;
  margin: 0 auto;
  padding: 0 15px;
}
.container--min {
  max-width: 1250px;
}

@media (max-width: 1280px) {
  .container {
    max-width: 1230px;
  }
  .container--min {
    max-width: 1030px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 758px;
  }
  .container--min {
    max-width: 634px;
  }
}
html {
  box-sizing: border-box;
  min-height: 100%;
}

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  font-family: "Formular", sans-serif;
  color: #2C2E3E;
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}

a[href^="tel:"] {
  white-space: nowrap;
}

input, button, textarea {
  font-family: "Formular", sans-serif;
  outline: none;
  -webkit-appearance: none;
}

button {
  background: none;
}

textarea {
  resize: none;
}

input[type=submit] {
  cursor: pointer;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
}

h1 {
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 48px;
  line-height: 55px;
}

h2 {
  margin: 0 0 12px;
  font-weight: 700;
  font-size: 35px;
  line-height: 40px;
}

h3 {
  margin: 0 0 8px;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
}

h5 {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
}

a {
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
a:hover {
  text-decoration: none;
}

body.overflow {
  height: 100vh;
  overflow: hidden;
}

body {
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: auto;
}

.subtitle {
  margin-bottom: 24px;
  font-size: 24px;
  line-height: 28px;
}

@keyframes fadeInUp {
  0% {
    display: none;
    opacity: 0;
    transform: translateY(-100%);
  }
  1% {
    display: block;
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    display: block;
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 20px 0;
  background: #2C2E3E;
  color: #fff;
}
.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: block;
  width: 75px;
}
.logo__img {
  display: block;
}

.main-menu {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-menu__item {
  margin-right: 46px;
}
.main-menu__link {
  display: block;
  padding: 10px 20px;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #fff;
  border-radius: 100px;
}
.main-menu__link:hover {
  background: rgba(143, 149, 172, 0.2);
}
.main-menu__link:active, .main-menu__link.active {
  background: #FF2D23;
}
.main-menu__link--parent {
  position: relative;
  padding-right: 40px;
}
.main-menu__link--parent::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  width: 8px;
  height: 8px;
  background: url("../img/sprite.svg#angle-down-white") no-repeat center;
  background-size: contain;
}